* {
  margin:0;
  padding:0;
}
label,input,textarea,select {
  font-size:1em;
}

#forms {
  width:49%;
  padding:10px;
  float:left;
}
#forms fieldset {
  padding:10px;
  margin-bottom:10px;
}
#forms .row label,
#forms .row > p:first-child,
#forms .row > input:first-child {
  display:block;
  float:left;
  min-width:15em;
  text-align:right;
  margin-right:10px;
}
#forms .row label:first-child {
  margin-top:5px;
}

#forms .row label:after {
  content:":";
}

#forms .row#deleted {
  color:red;
  text-align:center;
  margin-top: 1em;
  display:none;
}

#forms .row > * + * {
  margin-bottom:0.5em;
}

#forms .row * + #_id + button { /* 'Save'-Button */
  width:5em;
  height:2em;
  margin-left:2em;
  margin-right:2em;
}

#forms #freetags p {
  float:left;
}
#forms #freetags button#plus {
  width:5em;
  height:2em;
  margin-left:2em;
  margin-top:-0.5em;
}

#forms button#delete {
  height:2em;
  padding:0 1em;
}

#forms .row input,
#forms .row * + textarea {
  width:15em;
  padding:0.2em;
}
#forms .row #_id {
  width:18em;
  text-align:center;
}

#map {
  width: 49%;
  height: 90vh;
  border:1px solid black
}
